<!-- home/detailBox2 左图右文 -->
<template>
    <div>
        <div class="box">
            <img :src="src" class="list_img">
            <div class="list_desc">
                <p class="title">{{title}}</p>
                <p class="remark">{{remark}}</p>
                <div class="mark">
                    <span class="green">政策法规</span>
                    <span class="time">2019-06-14</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['title', 'remark', 'src'],
}

</script>
<style lang='less' scoped>
    .box {
        margin-bottom: .4rem;
        position: relative;
        padding: .2rem 0;
        margin: 0 .15rem;
        border-bottom: 1px solid #f2f2f2;
        .list_img {
            width: 2.2rem;
            height: 1.7rem;
            border-radius: 5px;
            margin-right: .1rem;
            vertical-align:top;
        }
        .list_desc {
            vertical-align:top;
            display: inline-block;
            margin: 0 auto;
            opacity: .8;
            text-align: left;
            word-break: break-all;
            .remark {
                opacity: .7;
            }
            .mark {
                position: absolute;
                bottom: .2rem;
                right: .2rem;
                font-size: 12px;
                .green {
                    color: #0c8484;
                    margin-right: .1rem;
                }
                .time {
                    opacity: .7;
                }
            }
        }
    }

    
</style>


