<!-- home/detailBox1 上图下文 -->
<template>
    <div>
        <div class="box">
            <div>
                <img :src="src" class="list_img">
            </div>
            <div class="list_desc">{{desc}}</div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['desc', 'src'],
}

</script>
<style lang='less' scoped>
    .box {
        text-align: center;
        margin-bottom: .4rem;
        padding: .1rem;
        .list_img {
            width: 100%;
            height: 1.5rem;
            border-radius: 3px;
        }
        .list_desc {
            opacity: .8;
            text-align: left;
            word-break: break-all;
        }
    }

    
</style>


