<!-- home/mainNav -->
<template>
    <div class="box" :style="{ marginBottom: mButton }">
        <div>
            <img :src="src" class="nav_img">
        </div>
        <div class="nav_title">{{title}}</div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['src', 'title', 'mButton'],
    components: {},
    methods: {}
}

</script>
<style lang='less' scoped>
    .box {
        text-align: center;
        padding-top: .2rem;
        .nav_img {
            width: .8rem;
            height: .8rem;
            margin-bottom: .1rem;
        }
        .nav_title {
            opacity: .8;
        }
    }

    

</style>


