<!-- home/detailBox3 全图 -->
<template>
    <div>
        <div class="box">
            <img :src="src" class="list_img">
            <span class="top_mark">{{topMark}}</span>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['src', 'topMark'],
}

</script>
<style lang='less' scoped>
    .box {
        text-align: center;
        padding: .1rem;
        position: relative;
        .list_img {
            border-radius: 4px;
            width: 100%;
            height: 2.2rem;
        }
        .top_mark {
            position: absolute;
            left: .1rem;
            top: .1rem;
            color: #fff;
            background-color: rgba(0, 0, 0, .5);
            text-align: center;
            padding: .1rem;
            border-radius: 4px 0 6px 0;
            font-size: 12px;
        }
    }
    
</style>


