<!-- home/carouselWithDesc -->
<template>
    <div class="with_desc_carousel">
        <img :src="src">
        <div class="carousel_desc">
            <div class="carousel_desc_title">{{title}}</div>
            <div>{{desc}}</div>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['src', 'title', 'desc'],
}

</script>
<style lang='less' scoped>
    .with_desc_carousel {
        position: relative;
        img {
            border-radius: 5px;
            height: 4rem;
            width: 100%;
        }
        .carousel_desc {
            position: absolute;
            background-color: rgba(0, 0, 0, .3);
            color: #fff;
            padding: .05rem .1rem;
            bottom: 2px;
            left: 0;
            width: 100%;
            border-radius: 0 0 5px 5px;
            .carousel_desc_title {
                font-size: 16px;
            }
        }
    }
</style>


