<template>
  <div id="app">
    <el-header>
      <i class="el-icon-arrow-left"></i>
      <div class="title">{{title | filterTitle}}</div>
    </el-header>
    <router-view/>
    <div class="fix_bottom_nav">
      <el-row>
          <el-col :span="8" class="nav_item"  v-for="item in bottomNavList" :key="item.id">
              <mainNav :title="item.title" :src="item.src" :mButton="'.1rem'"></mainNav>
          </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import mainNav from '@/components/mainNav'
export default {
    name: 'App',
    data() {
      return  {
        title: '中国-东盟型智慧城市协同创新中心',
        bottomNavList: [
            {
                id: 3012,
                title: '协同',
                src: require('@/assets/angle.png')
            },
            {
                id: 2014,
                title: '项目',
                src: require('@/assets/angle.png')
            },
            {
                id: 5214,
                title: '活动',
                src: require('@/assets/angle.png')
            },
        ]
      }
    },
    components: {mainNav},
    filters: {
      filterTitle(val) {
        return val.length > 8 ? val.slice(0, 8) + '...' : val;
      }
    }
}
</script>
<style scoped lang="less">
  .fix_bottom_nav {
      position: fixed;
      bottom: -1px;
      background-color: #fff;
      width: 100%;
      z-index: 19950606;
      padding: .1rem;
      box-shadow: 0 0 5px 0 #ccc;
      background-attachment: fixed;
  }

  .el-header, .el-footer {
    background-color: #0d92eb;
    color: #fff;
    line-height: 60px;
    position: relative;
    .el-icon-arrow-left {
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
    .title {
      text-align: center;
      font-size: 18px;
    }
  }

</style>



