<!-- home/globalTitle -->
<template>
    <div class="box">
       <div class="left_item">
           <span class="blue_dot"></span>
           <span class="title">{{title}}</span>
       </div>
       <div class="right_item">
           <i class="el-icon-arrow-right"></i>
       </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
        };
    },
    props: ['title']
}

</script>
<style lang='less' scoped>
    .box {
        overflow: hidden;
        padding: .1rem .2rem;
        margin-bottom: .2rem;
        position: relative;
        .left_item {
            float: left;
        }
        .right_item {
            float: right;
        }
        .blue_dot {
            display: inline-block;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #0d9deb;
        }
        .title {
            margin-left: 12px;
            font-size: 16px;
        }
    }
</style>


